import React from "react";
import ClasseItem from "./ClasseItem";
import "./ClasseList.css";

const ClasseList = (props) => {
  return (
    <ul className="classe-list">
      {props.classes.map((c) => (
        <ClasseItem
          setClasse={props.setClasse}
          selectedClasse={props.selectedClasse}
          key={c.id}
          classe={c}
          annee={props.annee && props.annee}
          filiere={props.filiere && props.filiere}
        />
      ))}
    </ul>
  );
};

export default ClasseList;
