import {
  handleResponse,
  checkRoleAutorizationFail,
  authHeader,
  fetchResponse,
} from "./midlwares";
import { SERVEUR_URL } from "../config/config";
const USER_API = SERVEUR_URL + "/api/user";

/**
 * Register a new client.
 * @param {*} data
 */
const registerCient = (data) => {
  const params = ["username", "password", "wilaya", "commune", "phone"];
  const keys = Object.keys(data);
  if (!params.every((key) => keys.includes(key)))
    return Promise.reject({
      message: "Les paramètres de formulaire sont invalides",
    });

  const requestOptions = {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(data),
  };

  return fetch(`${USER_API}/clients/signup`, requestOptions).then(
    handleResponse
  );
};

/**
 *
 * @param {userType, code, type, classesIds, deviceDetect} data
 */
const createAbonnement = (data) => {
  if (checkRoleAutorizationFail(data))
    return Promise.reject({
      message: "Vous n'avez pas le droit de faire cette action",
    });

  const params = [
    "userType",
    "code",
    "type",
    "duree",
    "deviceDetect",
    "classesIds",
  ];
  const keys = Object.keys(data);
  if (!params.every((key) => keys.includes(key)))
    return Promise.reject({
      message: "Les paramètres de formulaire sont invalides",
    });

  const requestOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      ...authHeader(),
    },
    body: JSON.stringify(data),
  };

  return fetch(`${USER_API}/abonnements/new`, requestOptions).then(
    handleResponse
  );
};

/**
 * Sign In client (user).
 * @param {*} data
 */
const signInClient = (data) => {
  const params = ["username", "password", "deviceDetect"];
  const keys = Object.keys(data);
  if (!params.every((key) => keys.includes(key)))
    return Promise.reject({
      message: "Les paramètres de formulaire sont invalides",
    });

  const requestOptions = {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(data),
  };

  return fetch(`${USER_API}/clients/signin`, requestOptions).then(
    handleResponse
  );
};

const signInEleve = (data) => {
  if (checkRoleAutorizationFail(data))
    Promise.reject({
      message: "Non autorisé pour effectuer cette tâche",
    });

  const params = ["eleveId", "classeId", "userType"];
  const keys = Object.keys(data);
  if (!params.every((key) => keys.includes(key)))
    return Promise.reject("Les paramètres indiqués sont incorrectes");

  let requestOption = {
    method: "POST",
    headers: {
      "Content-Type": "Application/json",
      ...authHeader(),
    },
    body: JSON.stringify(data),
  };

  return fetch(`${USER_API}/eleves/signin`, requestOption).then(fetchResponse);
};

const accountService = {
  registerCient,
  createAbonnement,
  signInClient,
  signInEleve,
};

export default accountService;
