import { connect } from "react-redux";
import React, { useState } from "react";

import AccountService from "../../../../shared/services/account-service";
import Toasteo from "toasteo";
import "toasteo/dist/css/toasteo.css";
import "../../../../auth/pages/Inscription.css";
import "./LoginEleve.css";
import LoadingSpinner from "../../../../shared/components/UIElements/LoadingSpinner";
import { setEleveData } from "../../../../store/actions";
import { ReactComponent as EditIcon } from "./editEleve.svg";

const LoginEleve = (props) => {
  const [isSubmitting, setTisSubmitting] = useState(false);

  const cycle = {
    1: "Primaire",
    2: "Cem",
    3: "Lycée",
  };

  const color = {
    1: "primaire",
    2: "cem",
    3: "lycee",
  };
  const connectEleve = (eleve) => {
    const data = {
      userType: "client",
      eleveId: eleve._id,
      classeId: eleve.classe._id,
    };
    setTisSubmitting(true);
    window.Toasteo = new Toasteo({});
    // Send to back end
    AccountService.signInEleve(data)
      .then((res) => {
        setTisSubmitting(false);
        window.Toasteo.success("Elève Connecté!");

        let eleve = res.eleve;
        eleve.classe = res.classe;
        // Set Redux state and localstorage entries
        props.confirmLogin({
          client: res.client,
          token: res.token,
          eleve,
        });
        // Redirect user to Dashboard (new abonnement in real)
      })
      .catch((err) => {
        window.Toasteo.error(err.message || "echèc d'authentification");
        setTisSubmitting(false);
      });
  };

  return (
    <div className="login-eleves">
      {isSubmitting && <LoadingSpinner />}
      <ul className="eleves-option">
        {props.eleves.map((eleve, index) => (
          <li className="eleves-option__eleve" key={index}>
            <div className="eleves-option__eleve--header">
              <div className="eleve--header__avatar">
                <div
                  className="eleve--header__avatar-bg"
                  onClick={(e) => connectEleve(eleve)}
                  style={
                    eleve.avatar
                      ? {
                          background: `url(${eleve.avatar}) center center / cover`,
                        }
                      : {
                          background:
                            "#c4c4c4 url(/assets/img/student-avatar.png) center center / cover",
                        }
                  }
                ></div>
                <div
                  onClick={(e) => connectEleve(eleve)}
                  className="eleve--header__edit-icon"
                >
                  <EditIcon />
                </div>
              </div>
              <h2>{eleve.nom}</h2>
              <hr />
            </div>
            <div
              className={`eleves-option__eleve--info color-${
                color[eleve.classe.cycle]
              }`}
            >
              <p>
                Classe:{" "}
                <span className="color-lycee">{eleve.classe.codeCl}</span>
              </p>
              <p>
                Cycle:{" "}
                <span className="color-cem">{cycle[eleve.classe.cycle]}</span>
              </p>
              {eleve.classe.filiere && (
                <p>
                  Filiere:{" "}
                  <span className="color-primaire">{eleve.classe.filiere}</span>
                </p>
              )}
              <p>
                Sexe: <span className="color-cem">{eleve.sexe}</span>
              </p>
              <p>
                Age: <span className="color-cem">{eleve.age}</span>
              </p>
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    client: state.client,
    eleves: state.client.eleves,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    confirmLogin: (params) => {
      dispatch(setEleveData(params));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LoginEleve);
