import React, { useState, useRef, useEffect } from "react";
import Swiper from "swiper";

import "./Quiz.css";
import DashboardQuiz from "./DashboardQuiz";
import CardsQuiz from "./CardsQuiz";
import Main from "../../components/Main";
import SommaireQuiz from "./SommaireQuiz";

const Quiz = () => {
  const [isShowQuiz, setIsShowQuiz] = useState(false);
  const [matiereQuiz, setMatiereQuiz] = useState("");
  const [coursQuiz, setCoursQuiz] = useState({});
  let swiper = useRef(null);

  const selectMatiereQuiz = (matiere) => {
    console.log(matiere);
    setMatiereQuiz(matiere);
    swiper.current.slideNext();
  };

  const selectQuizCours = (cours) => {
    setCoursQuiz(cours);
    setIsShowQuiz(true);
  };
  const hideQuiz = () => {
    setIsShowQuiz(false);
  };

  useEffect(() => {
    swiper.current = new Swiper(".quiz-swiper", {
      allowTouchMove: false,
      navigation: {
        prevEl: ".slide-prev",
      },
    });
  }, []);
  return (
    <React.Fragment>
      <Main className="main-quiz">
        <CardsQuiz
          show={isShowQuiz}
          hideQuiz={hideQuiz}
          matiereQuiz={matiereQuiz}
        />
        <div className="quiz-swiper">
          <div className="swiper-wrapper">
            <div className="swiper-slide">
              <DashboardQuiz
                className={isShowQuiz && "hide"}
                onClick={selectMatiereQuiz}
              />
            </div>
            <div className="swiper-slide">
              <SommaireQuiz
                matiereQuiz={matiereQuiz}
                itemQuiz={coursQuiz}
                showQuiz={selectQuizCours}
              />
            </div>
          </div>
        </div>
      </Main>
    </React.Fragment>
  );
};

export default Quiz;
