import React from "react";
import "./AvatarClasse.css";

const AvatarClasse = (props) => {
  const cycle = {
    1: "primaire",
    2: "cem",
    3: "lycee",
  };
  let className = "";
  let content = props.id;
  if (props.active) {
    className = "active";
  } else {
    if (props.classe.codeCl) {
      className = `${cycle[props.classe.cycle]}-select`;
      content = props.classe.codeCl;
    }
  }

  return (
    <div className={`avatar-classe  ${className}`} onClick={props.onClick}>
      <div className="avatar">{content}</div>
    </div>
  );
};

export default AvatarClasse;
