import React, { useEffect } from "react";

import "./CoursList.css";
import CourItem from "./CourItem";

const CoursList = (props) => {
  useEffect(() => {}, [props]);

  return props.classe.matieres.map((m, key) => {
    return (
      <CourItem
        key={m._id}
        className={`bg-${key % 4}`}
        trimestre={
          props.eleve.last_viewed_courses &&
          props.eleve.last_viewed_courses.find((el) => el._id === m._id)
            ? props.eleve.last_viewed_courses.find((el) => el._id === m._id)
                .temps
            : "1er Trimestre"
        }
        // trimestre="1er Trimestre"
        lessonsCount={30}
        matiere={m}
        showVideos={props.showVideos}
      />
    );
  });
};

export default CoursList;
