import React, { useState } from "react";
import { connect } from "react-redux";

import Backdrop from "../../../shared/components/UIElements/Backdrop";
import Modal from "../../../shared/components/UIElements/Modal";
import LoginEleve from "../components/dashboard/LoginEleve";

import DashboardCours from "./DashboardCours";
import Videos from "./Videos";

const Cours = (props) => {
  const [isShowVideos, setIsShowVideos] = useState(false);
  const [matiereVideos, setMatiereVideos] = useState("");

  const showVideos = (matiere) => {
    setMatiereVideos(matiere);
    setIsShowVideos(true);
  };
  const hideVideos = () => {
    setIsShowVideos(false);
  };

  const DUMMY_CLASSE = {
    matieres: [
      {
        _id: 1,
        titreAr: "رياضيات",
        titreFr: "mathématiques",
      },
      {
        _id: 2,
        titreAr: "لغة عربية",
        titreFr: "arabe",
      },
      {
        _id: 3,
        titreAr: "لغة فرنسيّة",
        titreFr: "français",
      },
    ],
  };
  return (
    <React.Fragment>
      <Videos
        show={isShowVideos}
        hideVideos={hideVideos}
        matiere={matiereVideos}
      />
      {props.completelogin ? (
        <div>
          <Backdrop className=" high-zindex backdrop" />
          <Modal className="high-zindex completelogin">
            <LoginEleve />
          </Modal>
          <DashboardCours
            showVideos={showVideos}
            eleve={props.eleve}
            classe={DUMMY_CLASSE}
          />
        </div>
      ) : (
        !isShowVideos && (
          <DashboardCours
            showVideos={showVideos}
            eleve={props.eleve}
            classe={props.current_classe}
          />
        )
      )}
    </React.Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    client: state.client,
    eleve: state.eleve ? state.eleve : state.client.eleves[0],
    current_classe: state.eleve && state.eleve.classe && state.eleve.classe,
  };
};

export default connect(mapStateToProps, null)(Cours);
