import React, { useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Navigation } from "swiper";
import Button from "../../shared/components/FormElements/Button";

import ClasseList from "./ClasseList";

SwiperCore.use([Navigation]);

const ClasseModal = (props) => {
  return (
    <div>
      <div className="header">
        <p>
          {props.annee ? "Choisissez votre année" : "Choisissez votre filière"}
        </p>
        <hr />
      </div>
      <div className="content">{props.children}</div>
      <div className="actions">
        <Button
          className={`${props.className && props.className}`}
          onClick={props.onClick}
          type="button"
        >
          {props.action}
        </Button>
      </div>
    </div>
  );
};

const ClasseSelect = (props) => {
  const [selectedClasse, setClasse] = useState(props.classes[0]);
  const lycee = [
    {
      annee: 1,
      id: 1,
    },
    {
      annee: 2,
      id: 2,
    },
    {
      annee: 3,
      id: 3,
    },
  ];
  const [selectedAnneeLycee, setAnneeLycee] = useState(lycee[0]);
  if (props.cycle === "lycee") {
    const selectAnneeLycee = (classeLycee) => {
      setAnneeLycee(classeLycee);
      const firstClasse = props.classes.filter(
        (c) => c.annee === classeLycee.annee
      )[0];
      setClasse(firstClasse);
    };
    return (
      <Swiper
        allowTouchMove={false}
        style={{ height: "100%" }}
        navigation={{
          nextEl: ".continuer",
        }}
      >
        <SwiperSlide key={1}>
          <ClasseModal annee action="CONTINUER" className="continuer">
            <ClasseList
              classes={lycee}
              setClasse={selectAnneeLycee}
              selectedClasse={selectedAnneeLycee}
              annee
            />
          </ClasseModal>
        </SwiperSlide>
        <SwiperSlide key={2}>
          <ClasseModal
            filiere
            action="CONFIRMER"
            onClick={() => props.onConfirmer(selectedClasse)}
            className={props.classNameConfirmer}
          >
            <ClasseList
              classes={props.classes.filter(
                (c) => c.annee === selectedAnneeLycee.annee
              )}
              setClasse={setClasse}
              selectedClasse={selectedClasse}
              filiere
            />
          </ClasseModal>
        </SwiperSlide>
      </Swiper>
    );
  } else {
    return (
      <ClasseModal
        annee
        action="CONFIRMER"
        onClick={() => props.onConfirmer(selectedClasse)}
        className={props.classNameConfirmer}
      >
        <ClasseList
          classes={props.classes}
          setClasse={setClasse}
          selectedClasse={selectedClasse}
          annee
        />
      </ClasseModal>
    );
  }
};

export default ClasseSelect;
