import React from "react";

import "./ItemSommaire.css";
const ItemSommaire = (props) => {
  const onClickSommaire = () => {
    if (props.openVideo) {
      props.openVideo({ id: props.id, url: props.url });
      return;
    }
    if (props.showQuiz) {
      props.showQuiz(props.item);
      return;
    }
  };
  // console.log(props);
  return (
    <li className="sommaire-item">
      <button onClick={onClickSommaire}>
        <p className={props.className}>
          Leçon {props.id}: Ceci est un titre Ouffissime
        </p>
      </button>
    </li>
  );
};

export default ItemSommaire;
