import {
  handleResponse,
  checkRoleAutorizationFail,
  authHeader,
} from "./midlwares";
import { SERVEUR_URL } from "../config/config";
const USER_API = SERVEUR_URL + "/api/user";

/**
 * Fetch all courses names and videos of a specifi matiere for a specific classe.
 * @param {*} data
 */
const getMatiereVideos = (data) => {
  if (checkRoleAutorizationFail(data, "eleve"))
    return Promise.reject({
      message:
        "Non autorisé! Uniquement l'élève concerné qui peut effectuer cette tâche",
    });
  const params = ["userType", "matiereId", "classeId"];
  const keys = Object.keys(data);
  if (!params.every((key) => keys.includes(key)))
    return Promise.reject({
      message: "Les paramètres de formulaire sont invalides",
    });
  const requestOptions = {
    method: "GET",
    headers: { ...authHeader("eleve"), userType: data.userType },
  };

  return fetch(
    `${USER_API}/cours/classe/matiere/index/${data.classeId}/${data.matiereId}`,
    requestOptions
  ).then(handleResponse);
};

