/**
 * preview Ad.
 * @param {*} data
 */
const previewAd = (data) => {
  if (checkRoleAutorizationFail(data))
    return Promise.reject({
      message:
        "Non autorisé! Uniquement l'élève concerné qui peut effectuer cette tâche",
    });
  const params = ["adId"];
  const keys = Object.keys(data);
  if (!params.every((key) => keys.includes(key)))
    return Promise.reject({
      message: "Les paramètres de formulaire sont invalides",
    });
  const requestOptions = {
    method: "GET",
    headers: { ...authHeader(), userType: data.userType },
  };

  return fetch(`${USER_API}/pubs/view/${data.adId}`, requestOptions).then(
    handleResponse
  );
};

const getClassesByCycle = (cycle) => {
  const requestOptions = {
    method: "GET",
    headers: {
      ...authHeader(),
      userType: "client",
    },
  };

  return fetch(`${USER_API}/classes/${cycle}`, requestOptions).then(
    handleResponse
  );
};

const contentService = {
  getMatiereVideos,
  getMatierePDFs,
  getMatiereQuizs,
  previewAd,
  getClassesByCycle,
};

export default contentService;
