import React, { useState } from "react";
import Button from "../../shared/components/FormElements/Button";
import "./FinalisationAbonnement.css";
import ClasseRecapitulatif from "../components/ClasseRecapitulatif";

const FinalisationAbonnement = (props) => {
  const [isTouched, setIsTouched] = useState(false);
  const [codeAbonnement, setCodeAbonnement] = useState("");

  const onChange = (e) => {
    setCodeAbonnement(e.target.value);
  };

  return (
    <div className="abonnement-step finalisation-abonnement">
      <div className="header">
        <h2>CODE D'ABONNEMENT</h2>
        <hr />
      </div>
      <div style={{ height: "100%" }} className="content">
        <div className="finalisation-abonnement__code-abonnement">
          <div className="code-input">
            <input type="text" onChange={onChange} />
            {!isTouched && <div className="input-icon"></div>}
          </div>
          <p className="indication">
            Veuillez inserer le code que vous avez reçu en envoyant #750
          </p>
          <Button onClick={() => props.onConfirmer(codeAbonnement)}>
            CONFIRMER
          </Button>
        </div>
        <div className="finalisation-abonnement__recapitulatif">
          <div className="title">
            <hr />
            <p>Récapitulatif</p>
            <hr />
          </div>
          <div className="recap">
            <p>Abonnement {props.type === "3cls" ? "3 Classes" : "1 Classe"}</p>
            <p>Durée: 3 Mois</p>
            {props.type !== "3cls" && props.selectedClasses ? (
              <ul className="classes center">
                <ClasseRecapitulatif
                  classe={props.selectedClasses}
                  type="1cls"
                />
              </ul>
            ) : (
              <ul className="classes">
                {props.selectedClasses.map((c, i) => (
                  <ClasseRecapitulatif classe={c} key={i} id={i} />
                ))}
              </ul>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default FinalisationAbonnement;
