import React, { useState } from "react";
import "./PhoneOption.css";
const PhoneOption = () => {
  const [showInfo, setShowInfo] = useState(false);
  const togglShowInfo = () => {
    setShowInfo(!showInfo);
  };

  return (
    <div className="phone-option">
      <div className={`phone-option__modal ${showInfo ? "" : "d-n"}`}>
        <div></div>
        <div></div>
        <p>Explication de pourquoi on a besoin du numéro de telephone</p>
      </div>
      <div className="phone-option__icon" onClick={togglShowInfo}>
        <p>?</p>
      </div>
    </div>
  );
};

export default PhoneOption;
