import * as actionTypes from "./actions";

const initState = () => {
  const cachedClient =
    localStorage.getItem("client") != null
      ? JSON.parse(localStorage.getItem("client"))
      : null;
  const cachedEleve =
    localStorage.getItem("eleve") != null
      ? JSON.parse(localStorage.getItem("eleve"))
      : null;
  const cachedMatiere =
    localStorage.getItem("current_matiere") !== null
      ? JSON.parse(localStorage.getItem("current_matiere"))
      : cachedEleve !== null
      ? cachedEleve.classe.matieres[1]
      : null;
  const initialState = {
    current_link: null,
    is_connecte: cachedClient !== null || false,
    is_eleve_connecte: cachedEleve !== null || false,
    is_abonne: (cachedClient !== null && cachedClient.is_abonne) || false,
    token: localStorage.getItem("token"),
    client: cachedClient,
    eleve: cachedEleve,
    current_matiere: cachedMatiere,
  };
  return initialState;
};

const reducer = (state = initState(), action) => {
  let newState = {};
  switch (action.type) {
    case actionTypes.CURRENTLINK:
      // Set the current page active in drawer sidebar
      return {
        ...state,
        current_link: action.value,
      };

    case actionTypes.CURRENTMATIERE:
      // Set the current page active in drawer sidebar
      return {
        ...state,
        current_matiere: action.value,
      };

    case actionTypes.CLIENT:
      // Save local storage!
      newState = {
        ...state,
        is_connecte: true,
        token: action.token,
        client: action.client,
        is_eleve_connecte: false,
        current_matiere: null,
        eleve: null,
      };

      if (action.client && action.client.is_abonne) {
        newState.is_abonne = true;
      }
      return newState;

    case actionTypes.DECONNECTION:
      localStorage.removeItem("client");
      localStorage.removeItem("token");
      if (state.is_abonne && state.eleve) {
        localStorage.removeItem("eleve");
        localStorage.removeItem("current_matiere");
      }
      return {
        ...state,
        is_connecte: false,
        is_abonne: false,
      };

    case actionTypes.ELEVE:
      // Set the authenticated eleve
      newState = {
        ...state,
        is_eleve_connecte: true,
        is_abonne: true,
        is_connecte: true,
        eleve: action.eleve,
        client: action.client,
        token: action.token,
        current_matiere: action.current_matiere,
      };
      return newState;

    default:
      return state;
  }
};

export default reducer;
