import React from "react";

import "./ListSommaire.css";
import ItemSommaire from "./ItemSommaire";
const ListSommaire = (props) => {
  return (
    <ul className="list-sommaire">
      {props.items.map((item) => (
        <ItemSommaire
          key={item.id}
          id={item.id}
          item={item}
          className={`${
            props.videoPlay && item.id === props.videoPlay.id ? "active" : ""
          } ${props.itemQuiz && item.id === props.itemQuiz.id ? "active" : ""}`}
          showQuiz={props.showQuiz && props.showQuiz}
          url={item.url && item.url}
          openVideo={props.openVideo && props.openVideo}
        />
      ))}
    </ul>
  );
};

export default ListSommaire;
