export const getWilayas = async () => {
  const response = await fetch("/assets/wilayas-fr.json");
  return response.json();
};

export const getCommunesByWilayaId = async (wilaya) => {
  const wilaya_id = wilaya.split("-")[0];
  const reponse = await fetch("/assets/communes-wilayas-fr.json");
  const communes = await reponse.json();
  return communes.filter((c) => c.wilaya_id === wilaya_id);
};

export const identifyDevice = (device) => {
  let detectedDevice;
  if (device.isMobile || device.isTablet) {
    detectedDevice = `${device.vendor} - ${device.model} (${device.os}: ${device.osVersion} - w: ${window.screen.width}, h: ${window.screen.height})`;
  } else if (device.isBrowser) {
    detectedDevice = `${device.osName} - ${device.osVersion} (${device.browserName}: ${device.browserMajorVersion} - w: ${window.screen.width}, h: ${window.screen.height})`;
  } else {
    detectedDevice = "Uknown";
  }
  return detectedDevice;
};
