import React, { useReducer, useEffect, useRef } from "react";
import { connect } from "react-redux";
import Swiper from "swiper";
import { deviceDetect } from "react-device-detect";

import "./Abonnement.css";
import ArchedHeader from "../../shared/components/UIElements/ArchedHeader";
import MainAbonnement from "../components/MainAbonnement";
import CycleAbonnement from "./CycleAbonnement";
import FinalisationAbonnement from "./FinalisationAbonnement";
import TypeAbonnement from "./TypeAbonnement";
import accountService from "../../shared/services/account-service";
import Toasteo from "toasteo";
import LoadingSpinner from "../../shared/components/UIElements/LoadingSpinner";
import { ABONNEMENT, setClientData, setEleveData } from "../../store/actions";
import { identifyDevice } from "../../shared/services/util";

const initialState = {
  abonnementInputs: {
    type: "",
    deviceDetect: "",
    classes: [],
  },
  step: 1,
  isLoading: false,
};

const abonnementReducer = (state, action) => {
  let classes;
  switch (action.type) {
    case "TYPE_ABONNEMENT":
      if (action.payload === "1cls") {
        classes = [];
      } else if (action.payload === "3cls") {
        classes = [{}, {}, {}];
      } else {
        classes = state.classes;
      }
      return {
        ...state,
        abonnementInputs: {
          ...state.abonnementInputs,
          type: action.payload,
          classes: classes,
          duree: 3,
        },
      };

    case "CLASSES_ABONNEMENT":
      let type;
      let typeAbonnement = {
        1: "1pri",
        2: "1cem",
        3: "1lyc",
      };
      if (state.abonnementInputs.type === "3cls") {
        classes = [...state.abonnementInputs.classes];
        classes[action.index] = action.classe;
        type = "3cls";
      } else {
        console.log(action.classe.cycle);
        type = typeAbonnement[action.classe.cycle];
        classes = [action.classe];
      }
      return {
        ...state,
        abonnementInputs: {
          ...state.abonnementInputs,
          classes: classes,
          type: type,
        },
      };

    case "CHANGE_STEP":
      return {
        ...state,
        step: action.payload % 4 === 0 ? 3 : action.payload,
      };

    case "RESET_CLASSES_ABONNEMENT":
      if (state.abonnementInputs.type === "3cls") classes = [{}, {}, {}];
      else classes = [];
      return {
        ...state,
        abonnementInputs: {
          ...state.abonnementInputs,
          classes,
        },
      };

    case "LOADING":
      return {
        ...state,
        isLoading: action.payload,
      };
    default:
      return state;
  }
};

const Abonnement = (props) => {
  const [abonnementState, dispatch] = useReducer(
    abonnementReducer,
    initialState
  );

  const onSelectTypeAbonnement = (type_abonnement) => {
    if (abonnementState.step === 1) {
      dispatch({ type: "TYPE_ABONNEMENT", payload: type_abonnement });
      slideNext();
    }
  };

  const onSelectClassesAbonnement = (classe, index = null) => {
    dispatch({ type: "CLASSES_ABONNEMENT", classe: classe, index: index });
    let classes = abonnementState.abonnementInputs.classes;
    if (abonnementState.abonnementInputs.type === "3cls") {
      const lastIndex = classes.findIndex((c) => Object.keys(c).length === 0);
      let elevesLeft = classes.filter((c) => Object.keys(c).length === 0);
      if (
        (elevesLeft.length === 1 && lastIndex === index) ||
        elevesLeft.length === 0
      ) {
        slideNext();
      }
    } else {
      slideNext();
    }
  };

  const swiper = useRef(null);
  useEffect(() => {
    swiper.current = new Swiper(".swiper-container", {
      allowTouchMove: false,
      navigation: {
        prevEl: ".prev-slide",
      },
    });
  }, []);

  const slideNext = () => {
    dispatch({
      type: "CHANGE_STEP",
      payload: abonnementState.step + 1,
    });
    swiper.current.slideNext();
  };

  const prevSlide = () => {
    if (abonnementState.step === 3)
      dispatch({ type: "RESET_CLASSES_ABONNEMENT" });
    dispatch({ type: "CHANGE_STEP", payload: abonnementState.step - 1 });
  };

  const onSubmit = (codeAbonnement) => {
    dispatch({ type: "LOADING", payload: true });
    const device = deviceDetect();
    let detectedDevice = identifyDevice(device);

    //deviceDetect gonna have the following format for example: Google - Pixel 2 (Android: 8.0)
    const body = {
      userType: "client",
      code: codeAbonnement,
      type: abonnementState.abonnementInputs.type,
      duree: 3,
      deviceDetect: detectedDevice,
      classesIds: abonnementState.abonnementInputs.classes.map((c) => c.id),
    };

    window.Toasteo = new Toasteo({ duration: 2000 });
    accountService.createAbonnement(body).then(
      (data) => {
        window.Toasteo.success("Abonnement Réussie!");
        if (body.type === "3cls") {
          props.sabonner({
            type: ABONNEMENT,
            token: data.token,
            client: data.client,
          });
        } else {
          let eleve = data.client.eleves[0];
          eleve.classe = data.classe;
          props.sabonner({
            nbClasse: 1,
            token: data.token,
            client: data.client,
            eleve,
          });
        }
        dispatch({ type: "LOADING", payload: false });
      },
      (error) => {
        dispatch({ type: "LOADING", payload: false });
        console.log(error);
        window.Toasteo.error(error || "Echèc d'abonnement");
      }
    );
  };

  const AbonnementSteps = [
    <TypeAbonnement
      id={1}
      onSelectTypeAbonnement={onSelectTypeAbonnement}
      classNameConfirmer="next-slide"
    />,
    <CycleAbonnement
      id={2}
      onSelectClassesAbonnement={onSelectClassesAbonnement}
      type={abonnementState.abonnementInputs.type}
      selectedClasses={abonnementState.abonnementInputs.classes}
      classNameConfirmer="next-slide"
    />,
    <FinalisationAbonnement
      id={3}
      classNameConfirmer="next-slide"
      selectedClasses={abonnementState.abonnementInputs.classes}
      type={abonnementState.abonnementInputs.type}
      onConfirmer={onSubmit}
    />,
  ];

  return (
    <div className="abonnement">
      {abonnementState.isLoading && <LoadingSpinner />}
      <ArchedHeader
        className={abonnementState.step === 3 ? "small" : ""}
        iconClassName={`prev-slide ${abonnementState.step === 1 ? "d-n" : ""}`}
        backIcon
        onIconClick={prevSlide}
      />
      <MainAbonnement
        className={abonnementState.step === 3 ? "tall" : ""}
        footerPicture={abonnementState.step === 3 ? false : true}
      >
        <div className="swiper-container abonnement__swiper">
          <div className="swiper-wrapper">
            {AbonnementSteps.map((c, i) => (
              <div className="swiper-slide" key={i}>
                {c}
              </div>
            ))}
          </div>
        </div>
      </MainAbonnement>
      <footer className="abonnement__footer">
        <button onClick={props.disconnect}>Deconnection</button>
      </footer>
    </div>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    disconnect: () => dispatch({ type: "DECONNECTION" }),
    sabonner: (params) => {
      let payload = {
        token: params.token,
        client: params.client,
      };
      if (params.nbClasse === 1) {
        payload.eleve = params.eleve;
        console.log(payload);
        dispatch(setEleveData(payload));
      } else {
        dispatch(setClientData(payload));
      }
    },
  };
};

export default connect(null, mapDispatchToProps)(Abonnement);
