import React, { useReducer, useEffect } from "react";
import Toasteo from "toasteo";

import "./CycleAbonnement.css";
import Modal from "../../shared/components/UIElements/Modal";
import Backdrop from "../../shared/components/UIElements/Backdrop";
import contentService from "../../shared/services/content-service";
import ClasseSelect from "../components/ClasseSelect";
import AvatarClasse from "../components/AvatarClasse";

let initialIndexEleve;
const classeReducer = (state, action) => {
  switch (action.type) {
    case "BACKDROP":
      return {
        ...state,
        classeInputs: {
          primaire: [],
          cem: [],
          lycee: [],
        },
        cycle: "",
        showModal: false,
      };
    case "SHOW_MODAL":
      return {
        ...state,
        classeInputs: {
          ...state.classeInputs,
          [action.cycle]: action.payload,
        },
        cycle: action.cycle,
        showModal: true,
      };
    case "INDEX_ELEVE":
      return {
        ...state,
        indexEleve: action.payload,
      };
    default:
      return state;
  }
};

const CycleAbonnement = (props) => {
  const [classeState, dispatch] = useReducer(classeReducer, {
    classeInputs: {
      primaire: [],
      cem: [],
      lycee: [],
    },
    cycle: "",
    showModal: false,
    indexEleve: -1,
    classes: [],
  });

  useEffect(() => {
    if (props.type === "3cls") {
      initialIndexEleve = props.selectedClasses.findIndex(
        (c) => Object.keys(c).length === 0 && c.constructor === Object
      );
      dispatch({ type: "INDEX_ELEVE", payload: initialIndexEleve });
    }
  }, [props.selectedClasses]);

  const getClasses = (idCycle, nameCycle) => {
    window.Toasteo = new Toasteo({ duration: 1500 });

    contentService
      .getClassesByCycle(idCycle)
      .then((data) => {
        dispatch({
          type: "SHOW_MODAL",
          payload: data.classes,
          cycle: nameCycle,
        });
      })
      .catch((error) => {
        window.Toasteo.error(error);
        console.log(error);
      });
  };

  const onConfirmer = (classe) => {
    if (props.type === "1cls") {
      props.onSelectClassesAbonnement(classe);
    } else {
      props.onSelectClassesAbonnement(classe, classeState.indexEleve);
    }
    dispatch({ type: "BACKDROP" });
  };

  const setIndexEleve = (index) => {
    dispatch({ type: "INDEX_ELEVE", payload: index });
  };

  return (
    <div className="abonnement-step cycle-abonnement">
      {classeState.showModal && (
        <React.Fragment>
          <Backdrop
            className="backdrop"
            onClick={() => {
              dispatch({ type: "BACKDROP" });
            }}
          />
          <Modal className="modal-classe">
            <ClasseSelect
              classes={classeState.classeInputs[classeState.cycle]}
              cycle={classeState.cycle}
              onConfirmer={onConfirmer}
              classNameConfirmer={props.classNameConfirmer}
            />
          </Modal>
        </React.Fragment>
      )}
      <div className="abonnement-step abonnement-cycle">
        {props.type === "1cls" ? (
          <div className="header">
            <h2>Veuillez choisir votre cycle</h2>
            <hr />
          </div>
        ) : (
          <div className="header multiple">
            <h2>Veuillez choisir le cycle des élèves</h2>
            <hr />
            <div className="eleves">
              {props.selectedClasses.map((c, i) => (
                <AvatarClasse
                  onClick={() => setIndexEleve(i)}
                  id={i + 1}
                  key={i}
                  classe={c}
                  active={classeState.indexEleve === i ? "active" : ""}
                />
              ))}
            </div>
          </div>
        )}
        <ul className={`content ${props.type === "3cls" ? "multiple" : ""}`}>
          <li className="list-item" onClick={() => getClasses(1, "primaire")}>
            <p>Primaire</p>
          </li>
          <li className="list-item" onClick={() => getClasses(2, "cem")}>
            <p>C.E.M</p>
          </li>
          <li className="list-item" onClick={() => getClasses(3, "lycee")}>
            <p>Lycée</p>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default CycleAbonnement;
