import React from "react";
import ReactDOM from "react-dom";
import "./Modal.css";

const Modal = (props) => {
  const modal = (
    <div className={`modal ${props.className && props.className}`}>
      {props.children}
    </div>
  );
  return ReactDOM.createPortal(modal, document.getElementById("modal-hook"));
};

export default Modal;
