import React from "react";

import "./CourItem.css";
import { ReactComponent as ArrowRight } from "../../img/arrow-right.svg";
import { ReactComponent as PlayButton } from "../../../components/img/play.svg";

// Redux
import { connect } from "react-redux";

const CoursItem = (props) => {
  return (
    <div>
      <div className="cour-item">
        <div className="cour-item__info">
          <h3>{props.matiere.titre}</h3>
          <button
            onClick={() => {
              props.setCurrentMatiere(props.matiere);
              props.showVideos(props.matiere);
            }}
            className="cour-item__info-more"
          >
            <h2>Voir plus</h2>
            <ArrowRight />
          </button>
        </div>
        <button
          onClick={() => {
            props.setCurrentMatiere(props.matiere);
            props.showVideos(props.matiere);
          }}
          className={`cour-item__thumbnail ${props.className}`}
        >
          <div className="cour-item__thumbnail-matiere">
            <h1>{props.matiere.titreFr}</h1>
            <h1 className="normal">{props.trimestre}</h1>
          </div>
          <PlayButton className="cour-item__thumbnail-play" />
          <p className="cour-item__thumbnail-lessons">
            {props.lessonsCount} Leçons
          </p>
        </button>
      </div>
    </div>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    setCurrentMatiere: (matiere) => {
      dispatch({ type: "CURRENTMATIERE", value: matiere });
      localStorage.setItem("current_matiere", JSON.stringify(matiere));
    },
  };
};

export default connect(null, mapDispatchToProps)(CoursItem);
