import React from "react";
import "./ClasseRecapitulatif.css";

const ClasseRecapitulatif = (props) => {
  let { classe, type } = props;
  if (type === "1cls") classe = classe[0];

  const cycle = {
    1: "Primaire",
    2: "CEM",
    3: "Lycée",
  };
  if (classe)
    return (
      <div
        className={`classe-recapitulatif ${props.type === "1cls" && "mono"} `}
      >
        {props.type === "1cls" ? (
          <li className="classe-id">Classe</li>
        ) : (
          <li className="classe-id">Classe {props.id + 1}</li>
        )}
        <li>{cycle[classe.cycle]}</li>
        <li>Année: {classe.annee}</li>
        <li>{classe.codeCl}</li>
        {classe.filiere && <li>{classe.filiere}</li>}
      </div>
    );
  else return <></>;
};

export default ClasseRecapitulatif;
