import React from "react";
import "./ClasseItem.css";

const ClasseItem = (props) => {
  return (
    <li
      className={`classe-item ${
        props.selectedClasse.id === props.classe.id ? "active" : ""
      } ${props.className && props.className}`}
      onClick={() => {
        props.setClasse(props.classe);
      }}
    >
      <div className="radio"></div>
      <p>
        {props.annee
          ? props.classe.annee === 1
            ? `1ère année`
            : `${props.classe.annee}éme année`
          : props.classe.filiere}
      </p>
    </li>
  );
};

export default ClasseItem;
